  margin-bottom: 0;
  padding-bottom: 50px;
  border-radius: 0;
  overflow-y: auto;
}
.content {
  width: 90%;
  max-width: 1680px;
  margin: auto;
}
.Home-subtitle {
  font-size: 2.1rem;
  font-weight: 300;
  padding-top: 2rem;
}
.user-instruction {
  z-index: 100;
  line-height: 35px;
  letter-spacing: 4px;
  // margin-left: 50%;
  // transform: translateX(-50%);
  text-align: left;
  margin-top: -25px;
}

.btn-content {
  width: 100%;
  border-radius: 12px;
  border: 2px solid #cfbf84;
}
a {
  text-decoration: none !important;
}
.grid-content {
  border-radius: 4px;
  height: 8.5rem;
  margin: 30px;
  padding: 2rem;
  font-size: 1.5rem;
  color: #000;
  transition: 0.4s;
  &:hover {
    opacity: 0.8;
    font-size: 1.6rem;
  }
}
.bg-white{
  background-color: rgba(243,187,92,0.08);
}
.bg-orange {
  background: #FFD286;
}
.bg-green {
  background: #E1E898;
}
.bg-pink {
  background: #FBCACA;
}
</style>



