<template>
  <div class="pb-2">
    <div v-if="allowMultiple"></div>
    <multiselect v-model="result" :options="features" :multiple="allowMultiple" :close-on-select="false" :clear-on-select="false" placeholder="" label="label" track-by="value">
      <template slot="singleLabel" slot-scope="props"><span class="option__desc"><span class="option__title">{{ props.option.label}}</span></span></template>
      <template slot="option" slot-scope="props">
        <div class="option__desc"><span class="option__title" :title="props.option.desc">{{ props.option.label }}</span></div>
      </template>
    </multiselect>
  </div>
</template>

<script>
export default {
  name: "SelectFeatures",
  data() {
    return {
      result: [],
      features: [
        { label: '', value: '',desc: '/' },
        { label: '', value: '',desc: '()' },
        { label: '', value: '',desc: '' },
        { label: '', value: '',desc: '' },
        { label: '', value: '',desc: '' },
        { label: '', value: '',desc: '' },
        { label: '', value: '',desc: '' },
        { label: '', value: '',desc: '()' },
        { label: '', value: '',desc: '' },
        { label: '', value: '',desc: '()' },
        { label: '', value: '',desc: '' },
        { label: '', value: '',desc: '()' },
        { label: '', value: '',desc: '' },
      ],
    };
  },
  props: {
    configKey: Object,
    rowIdx: Number,
    allowMultiple: Boolean
  },
  watch: {
    result: {
      handler(val) {
        this.$emit('featureUpdate', { key: this.configKey.key, rowIdx: this.rowIdx, value: val})
      },
      deep: true,
    },
  },
  methods: {
    clearAll() {
      this.result = []
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  @media (min-width: 1400px) {
    max-width: 1200px;
  }
  @media (min-width: 1000px) and (max-width: 1400px) {
    max-width: 900px;
  }
  @media (max-width: 1000px) {
    max-width: 700px;
  }
}

.column-container {
  @media (max-width: 768px) {
    max-width: 95%;
  }
}
.check-common-sentence {
  max-height: 400px;
  overflow-y: auto;
  overflow-x: hidden;
  border: solid 1px;
  border-color: lightgray;
}
.category-title {
  text-align: left;
  @media (min-width: 500px) {
    margin-left: 10%;
    min-width: 0px;
  }
  @media (max-width: 400px) {
    margin-left: -10%;
    min-width: 0px;
  }
  font-weight: 500;
  font-size: 20px;
}
.checkbox-box {
  width: 10%;
}
.checkbox-content {
  min-width: 70%;
  max-width: 70%;
  line-height: 40px;
  margin-left: 10%;
  @media (max-width: 400px) {
    margin-left: -20%;
    min-width: 0px;
    max-width: 90%;
  }
  li {
    text-align: left;
    font-size: 20px;
  }
}

// input[type="checkbox"] {
//   zoom: 180%;
// }

.custom-control-label::before,
.custom-control-label::after {
  top: 0.5rem;
  width: 1.5rem;
  height: 1.5rem;
}
</style>



