<template>
  <div class="pb-2">
    <el-row :gutter="2">
      <el-col :span="20">
        <textarea class="form-control border-secondary" rows="3" v-model="result"></textarea>
      </el-col>
      <el-col :span="4">
        <el-dialog title="" :visible.sync="dialogState[configKey.key]">
          <div v-for="statementGroup in sentencesExample[configKey.type]" :key="statementGroup.type" style="padding-bottom:20px;">
            <div style="padding-bottom: 5px;">{{statementGroup.type}}</div>
            <div v-for="statement in statementGroup.item" :key="statement.value">
              <div class='statement-card' :class="checkStatement(statement.value)?'isActive':''" @click="addStatement(statement.value)">
                {{statement.label}}
              </div>
            </div>
          </div>
        </el-dialog>
        <div class="example-btn" @click="dialogState[configKey.key]=true">
          <div class="example-btn-item"></div>
        </div>
      </el-col>
    </el-row>
</div>
</template>

<script>
export default {
  name: "InputDescription",
  data() {
    return {
      result: '',
      dialogState: {
        AA: false,
        AD: false,
        RA: false,
        RD: false,
      },
      sentencesExample: {
        A: [
          {
            type: '',
            item: [{
              label: "",
              value: ''
            }],
          },
          {
            type: '',
            item: [{
              label: '',
              value: '' }],
          },
          {
            type: '',
            item: [{ label: '', 
            value: '' }],
          },
          {
            type: '',
            item: [{ label: '',
            value: '' }],
          },
          {
            type: '',
            item: [{ label: '', value: '' }],
          },
          {
            type: '',
            item: [{ label: "", value: '' }],
          },
          {
            type: '',
            item: [{ label: '', value: '' }],
          },
          {
            type: '',
            item: [{ label: '', value: '' }],
          },
          {
            type: '',
            item: [
              { label: '', value: '' },
              { label: "", value: ''}]
          }
        ],
        D: [
          {
            type: '',
            item: [{ label: '', value: '' }],
          },
          {
            type: '',
            item: [{ label: '', value: '' }],
          },
          {
            type: '',
            item: [{ label: "", value: '' }],
          },
          {
            type: '',
            item: [{ label: "", value: '' }],
          },
          {
            type: '',
            item: [{ label: "", value: '' }],
          },
          {
            type: '',
            item: [{ label: "", value: '' }],
          },
          {
            type: '',
            item: [{ label: "", value: '' }],
          },
          {
            type: '',
            item: [{ label: '', value: '' }],
          },
          {
            type: '',
            item: [{ label: "", value: '' },
              { label: "", value: '' }],
          },
        ],
      },
    };
  },
  props: {
    configKey: Object,
    rowIdx: Number
  },
  watch: {
    result: {
      handler(val) {
        this.$emit('descriptionUpdate', { key: this.configKey.key, rowIdx: this.rowIdx, value: val})
      },
      deep: true,
    },
  },
  methods: {
    addStatement(statement) {
      this.isStartPredict = false;
      this.isLoading = false;
      this.maxResult = 0;
      if (this.checkStatement(statement)) {
        this.result = this.result.replace(statement, '');
      } else {
        this.result += statement;
      }
    },
    checkStatement(statement) {
      if (this.result.indexOf(statement) >= 0) return true;
      return false;
    },
    clearAll() {
      this.result = ''
    }
  }
};
</script>

<style lang="scss" scoped>
.form-control {
  height: 5rem !important;
}
.example-btn {
    border-radius: 4px;
    background-color: #5A4D30;
    width: 100%;
    height: 5rem !important;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    padding: 0 5px;
    text-align: center;
    cursor: pointer;
    transition: 0.4s;
    &:hover {
        opacity: 0.8;
    }
}
.example-btn-item {
  height: fit-content;
}
.statement-card {
  word-break: break-word;
  padding: 5px;
  margin-bottom: 5px;
  border: 1px solid #5A4D30;
  border-radius: 2px;
  cursor: pointer;
  transition: 0.4s;
  &:hover {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }
}
.isActive {
  background: #5A4D30;
  color: #fff;
}
</style>



