<template>
  <div class="page-container">
    <div class="predict-container">
      <div class="page-title">{{ pageText[$route.params.mode].title }}</div>
      <div class="shadow-none p-2 mb-2 rounded quote">{{ pageText[$route.params.mode].instruction }}</div>
      <div v-loading="isLoading">
        <router-view @resultUpdate="updateResult" ref="groupForm"/>
      </div>
      <div class="shadow-none p-2 mb-2">{{ pageText[$route.params.mode].note }}</div>
      <el-row :gutter="20">
        <el-col :span="12">
          <div class="predict-btn" style="background-color:#5A4D30; float:right;" @click="clearAllStatement()"></div>
        </el-col>
        <el-col :span="12">
          <div class="predict-btn" style="background-color:#F3BB5C" @click="startPredict()"></div>
        </el-col>
        <el-col :span="24">
          <PredictResult
            v-if="isStartPredict"
            id="predictResult"
            :predict_result="predict_result"
            :elapsedTime="elapsedTime"
            :isLoading="isLoading"
            :errorPrompt="errorPrompt"
            :errorCode="errorCode"
            :maxResult="maxResult"
          />
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import PredictResult from '../Sub/predictResult';

export default {
  name: 'UserPredict',
  components: {
    PredictResult,
  },
  data() {
    return {
      predict_result: { Applicant: 0, Respondent: 0, Both: 0 },
      elapsedTime: 0,
      maxResult: 0,
      isLoading: false,
      isStartPredict: true,
      errorPrompt: false,
      errorCode: new Error(),
      pageText: {
        mode1: {
          title: '',
          instruction: 'AI13()',
          note: 'AI',
        },
        mode2: {
          title: '',
          instruction: 'AI',
          note: 'AI',
        },
        mode3: {
          title: '',
          instruction: 'AI13()()',
          note: 'AI',
        },
      },
      resultConfig: [
        { title: 'The statements favorable to the party(you)', title_f: 'The factors favorable to the party(you)', type: 'A', key: 'AA', dialog: false },
        { title: 'The statements unfavorable to the party(you)', title_f: 'The factors unfavorable to the party(you)', type: 'D', key: 'AD', dialog: false },
        { title: 'The statements favorable to the other party', title_f: 'The factors favorable to the other party', type: 'A', key: 'RA', dialog: false },
        { title: 'The statements unfavorable to the other party', title_f: 'The factors unfavorable to the other party', type: 'D', key: 'RD', dialog: false },
      ],
      features: [
        { label: ' Parent-Child Affection', value: '' },
        { label: ' Parent\'s WIllingness and Capability', value: '' },
        { label: ' Parent\'s Financial Status', value: '' },
        { label: ' Supporting System', value: '' },
        { label: ' Parent\'s Living Condition', value: '' },
        { label: ' Primary Caregiver', value: '' },
        { label: ' Child\'s Age', value: '' },
        { label: ' Child\'s Development', value: '' },
        { label: ' Parent\'s Health Satus', value: '' },
        { label: ' Parent\'s Occupation', value: '' },
        { label: ' Child\'s Willingness', value: '' },
        { label: ' Parent\'s Friendliness to the Other', value: '' },
        { label: ' Parent\'s Character', value: '' },
      ],
      sentencesExample: {
        A: [
          {
            type: '',
            item: [{ label: "(The party has a legitimate job and sufficient financial capability to provide for themselves and their children's living needs, ensuring a stable and secure life for their offspring.)",
              value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The child has the ability to make judgments about which family members can provide a safe, satisfying psychological environment and take care of their daily needs. The child has expressed a willingness to live with the party.)',
              value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The child has a certain degree of dependence and sense of security towards the party, as evidenced by the child seeking comfort and assistance from the party and being soothed by them.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The party is currently the primary caregiver for the child, and is able to provide a stable and positive environment for their development. There is no indication of any improper care, and the interaction between the party and the child is good.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The community environment where the party resides is sufficient to provide for the basic necessities of life, including food, clothing, shelter, and recreational activities. The home itself is well-equipped, spacious, clean, and suitable for the child to live in.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: "(The party's family members and relatives also live nearby or together, and can provide support and assistance with the care of the child. The interaction between the child and the party's family members and relatives is natural and harmonious.)", value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The party is able to articulate their educational philosophy and parenting attitudes regarding the child, and they spend their free time and vacations accompanying their child for outdoor activities, nurturing parent-child interaction.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The party is willing to allow the other party to visit the child and maintain a parent-child relationship.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The party is actively seeking custody of the child with the motivation of providing a better environment for the child to grow up in.)', value: '' },
              { label: "(The child is still in infancy and early childhood and requires the care and nurturing of a mother, and the party has been living with the child long-term, thus having a deep understanding of the child's personality traits, physical and mental development, as well as their daily needs.)", value: '' }],
          },
        ],
        D: [
          {
            type: '',
            item: [{ label: '(The party involved currently has no job income or unstable income, and their living expenses rely on assistance from family members. It is uncertain whether they can support the financial needs of raising a child.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The child is old enough to understand the relationships between family members, and is less willing to live with the party involved.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: "(The child feels disgust towards the party involved for their various actions during the marriage, and experiences emotional damage due to the party's divorce, causing a strained and distant relationship between them.)", value: '' }],
          },
          {
            type: '',
            item: [{ label: "(The party involved currently does not live with the child or is not the primary caregiver of the child, so they may have insufficient understanding of the child's personality and needs.)", value: '' }],
          },
          {
            type: '',
            item: [{ label: "(The environment near the party involved's residence is complicated, and there is no independent living space for the child inside the house, so the home environment is not very suitable for the child's growth.)", value: '' }],
          },
          {
            type: '',
            item: [{ label: "(The party involved's other relatives and friends do not live together or have little contact, and their relationship with the child is unfamiliar, so they cannot provide sufficient support system.)", value: '' }],
          },
          {
            type: '',
            item: [{ label: "(In the past, the party involved had less time to accompany and interact with the child in their family life due to work or personality, and they also have limited understanding of the child's living conditions. The child feels anxious when alone with them.)", value: '' }],
          },
          {
            type: '',
            item: [{ label: '(The party involved is unwilling or obstructs the other party from visiting the child or having any contact.)', value: '' }],
          },
          {
            type: '',
            item: [{ label: "(If the party involved were to single-handedly raise the child, due to work-related reasons, they may not have enough time to discipline the child and may continue to let them engage in deviant behaviors, which is not conducive to the child's growth and development.)", value: '' },
              { label: "(The party involved's work schedule is unstable and often requires night shifts. While the child is still young and can adjust to the party involved's schedule, it may affect the child's education and daily routine in the future.)", value: '' }],
          },
        ],
      },
      result: {
        data: {
          AA: { Sentence: '', Feature: [] },
          AD: { Sentence: '', Feature: [] },
          RA: { Sentence: '', Feature: [] },
          RD: { Sentence: '', Feature: [] },
        },
      },
    };
  },
  methods: {
    updateResult(val) {
      this.result = val;
      console.log('result:', this.result);
    },
    addStatement(resultKey, statement) {
      this.isStartPredict = false;
      this.isLoading = false;
      this.maxResult = 0;
      if (this.checkStatement(resultKey, statement)) {
        this.result.data[resultKey].Sentence = this.result.data[resultKey].Sentence.replace(statement, '');
      } else {
        this.result.data[resultKey].Sentence += statement;
      }
    },
    checkStatement(resultKey, statement) {
      if (this.result.data[resultKey].Sentence.indexOf(statement) >= 0) return true;
      return false;
    },
    clearAllStatement() {
      console.log(this.$refs.groupForm);
      this.$refs.groupForm.clearAllStatement();
    },
    checkContainChinese(str) {
      return /[\u4E00-\u9FA5]+/g.test(str);
    },
    checkInputValid(data) {
      let isLenValid = false;
      let isLanguageValid = true;
      Object.keys(data).forEach((key) => {
        if (data[key].Sentence.length > 0) {
          isLenValid = true;
          if (!this.checkContainChinese(data[key].Sentence)) {
            isLanguageValid = false;
          }
        }
        if (data[key].Feature.length > 0) {
          isLenValid = true;
        }
        return null;
      });
      if (!isLenValid) {
        alert("You haven't input any custody factors or rationale statements.");
        return false;
      }
      if (!isLanguageValid) {
        alert('Now we only support chinese input.');
        return false;
      }


      return true;
    },
    findMaxResult(predictResult) {
      if (predictResult.Applicant > this.maxResult) {
        this.maxResult = predictResult.Applicant;
      }
      if (predictResult.Respondent > this.maxResult) {
        this.maxResult = predictResult.Respondent;
      }
      if (predictResult.Both > this.maxResult) {
        this.maxResult = predictResult.Both;
      }
    },
    mergeResult(inputData) {
      const outputData = {};
      Object.entries(inputData).forEach(([key, value]) => {
        const featuresSet = new Set();
        const sentences = [];
        console.log('key:', key, 'value:', value);

        value.forEach((item) => {
          if (item.Feature && item.Feature.length > 0) {
            item.Feature.forEach(feature => featuresSet.add(feature));
          }
          if (item.Sentence && item.Sentence.trim().length > 0) {
            sentences.push(item.Sentence.trim());
          }
        });

        outputData[key] = {
          Feature: Array.from(featuresSet),
          Sentence: sentences.join(' '),
        };
      });

      return outputData;
    },
    startPredict() {
      console.log('>>>>>start predict ==> raw result:', this.result.data);
      let result = this.mergeResult(this.result.data);
      console.log('>>>>>start predict ==> merge result:', result);
      if (this.checkInputValid(result)) {
        this.isLoading = true;
        result = {
          model: this.$route.params.mode,
          data: result,
        };
        console.log('>>>>>start predict ==> provide result:', result);
        axios({
          method: 'post',
          url: `${this.$api}/api/predict`,
          data: result,
        }).then((res) => {
          console.log('res.data:', res.data);
          // predict_result: { Applicant: 0, Respondent: 0, Both: 0 }
          this.predict_result = res.data;
          this.findMaxResult(this.predict_result);
          this.isStartPredict = true;
          this.isLoading = false;
        });
      } else {
        this.isStartPredict = false;
        this.isLoading = false;
        this.maxResult = 0;
      }
      // eslint-disable-next-line no-alert
    },
  },
  watch: {
    result: {
      handler(val) {
        // do stuff
        this.isStartPredict = false;
        this.isLoading = false;
        this.maxResult = 0;
      },
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.page-title {
    font-size: 1.5rem;
    font-weight: bold;
    width: fit-content;
    // border-bottom: 2px solid #F3BsB5C;
    // color: white;
    margin: 0px 0 20px 0;
    padding: 0 5px;
}
.predict-container {
    background-color: #fff;
    border: 2px solid #F3BB5C;
    border-radius: 8px;
    padding: 35px 40px;
    margin: 50px 0;
}
.ins-1 {
    font-size: 1.2rem;
    font-weight: bold;
    padding-top: 15px;
}
.quote {
    background-color: rgba(90,77,88,0.12);
    text-align: center;
    font-size: 1.2rem;
    text-align: left;
    margin: 10px 0;
}
.predict-btn {
    border-radius: 4px;
    color: #fff;
    font-size: 1.2rem;
    font-weight: bold;
    width: fit-content;
    text-align: center;
    padding: 10px 20px;
    cursor: pointer;
    transition: 0.4s;
    &:hover {
        opacity: 0.8;
    }
}
.statement-card {
  word-break: break-word;
  padding: 5px;
  margin-bottom: 5px;
  border: 1px solid #5A4D30;
  border-radius: 2px;
  cursor: pointer;
  transition: 0.4s;
  &:hover {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }
}
.isActive {
  background: #5A4D30;
  color: #fff;
}
.ins-3 {
  font-weight: bold
}
</style>



